import {Component} from '@angular/core';
import {IonicPage, LoadingController, NavController, NavParams} from 'ionic-angular';
import {ConfigProvider} from "../../providers/config/config";
import {LocationProvider} from "../../providers/location/location";
import {Geolocation} from "@ionic-native/geolocation";


@IonicPage()
@Component({
  selector: 'page-location-outer01',
  templateUrl: 'location-outer01.html',
})
export class LocationOuter01Page {

  locationOuterError01;
  searchingType;
  type;
  locationDetails;
  zoomVal;
  currentLocLat = 23.7265562;//Islami Bank head office lat  value
  currentLocLong = 90.4185879;//Islami Bank head office long value
  sortedDistanceList = [];
  noOfLocationNearestMe = 0;


  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public config: ConfigProvider,
              public geolocation: Geolocation,
              public locationService: LocationProvider,
              public loadingCtrl: LoadingController) {

    this.locationOuterError01 = "";
    this.type = this.navParams.get("type");
    this.locationDetails = this.navParams.get("locationDetails");
    this.zoomVal = this.config.mapZoomingMultiple;
    this.noOfLocationNearestMe = this.config.noOfLocationNearestMe;
    this.getMyCurrentPosition();
  }

  getMyCurrentPosition = function () {
    this.geolocation.getCurrentPosition().then((resp) => {
      this.currentLocLat = resp.coords.latitude;
      this.currentLocLong = resp.coords.longitude;
    }).catch((error) => {
      console.log('Error getting location', error);
    });
  };

  FindLocation02Action = function (sType) {

    this.searchingType=sType;

    if (this.searchingType == 1) {



      this.navCtrl.push('LocationOuter02Page', {
        'type': this.type,
        'searchingType': this.searchingType,
        'currentLocLat': this.currentLocLat,
        'currentLocLong': this.currentLocLong,
        'locationDetails': this.locationDetails
      });

    }

    if (this.searchingType == 2) {
      let loading = this.loadingCtrl.create({
        content: 'Please wait...',
        spinner: this.config.getLoadingSpinner()
      });

      this.MakeSortedList(this.currentLocLat, this.currentLocLong, JSON.parse(this.locationDetails));
      this.navCtrl.push('LocationOuter03Page', {
        'type': this.type,
        'searchingType': this.searchingType,
        'currentLocLat': this.currentLocLat,
        'currentLocLong': this.currentLocLong,
        'zoomVal': this.zoomVal,
        'locationDetails': JSON.stringify(this.sortedDistanceList)
      });
      loading.dismiss();
    }

  };

  MakeSortedList(curLat, curLong, locationList) {
    let newDistanceList = [];
    for (let result of locationList) {
      let lat = result.latitude;
      let long = result.longitude;
      let distance = this.locationService.getDistanceFromLatLonInKm(curLat, curLong, lat, long);
      result["dist"] = distance;
      newDistanceList.push(result)
    }
    newDistanceList.sort((a, b) => Number.parseFloat((a.dist )) - Number.parseFloat(b.dist));

    let loopVal = 0;
    for (let loc of newDistanceList) {
      if (loopVal < this.noOfLocationNearestMe) {
        this.sortedDistanceList.push(loc);
      }
      loopVal++;
    }
  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad LocationOuter01Page');
  }

}



